import { useEffect, useState } from 'react';

import Select from 'react-select';
import { Button } from 'react-bootstrap';

import searchOptions from '../data/searchOptions';
import classID from '../data/classID';
import mentors from '../data/mentor';

const Form = ({ search }) => {
    const [searchState, setSearchState] = useState(false);

    useEffect(() => {
        if (search !== false) {
            if (search === 'class') {
                setSearchState('class');
            }
            if (search === 'mentor') {
                setSearchState('mentor');
            }
        }
    }, [search]);

    return (
        <>
            <form>
                <div>
                    <label>Search Type</label>
                    {searchState === 'class' &&
                        <Select
                            value={searchOptions.filter(option => option.value === 'class')}
                            options={searchOptions}
                            onChange={selected => setSearchState(selected.value)}
                        />
                    }
                    {searchState === 'mentor' &&
                        <Select
                            value={searchOptions.filter(option => option.value === 'mentor')}
                            options={searchOptions}
                            onChange={selected => setSearchState(selected.value)}
                        />
                    }
                    {searchState === false &&
                        <Select options={searchOptions} onChange={selected => setSearchState(selected.value)} />
                    }
                </div>
                {
                    searchState === 'class' ?
                        <div>
                            <label>Class ID</label>
                            <Select options={classID} isSearchable={true} />
                        </div> : null
                }
                {
                    searchState === 'mentor' ?
                        <div>
                            <label>Mentor Name</label>
                            <Select options={mentors} isSearchable={true} />
                        </div> : null
                }

            </form>

            <Button variant='primary'>Search</Button>
            <Button variant='secondary'>Reset</Button>
        </>
    );
};

export default Form;