import '../styles/Search.css';
import { Card } from 'react-bootstrap';
import Form from './Form';

const Search = ({ search }) => {
    return (
        <>
            <Card className="card-container">
                <Card.Body>
                    <Card.Title className='card-title'>Search</Card.Title>
                    <Form search={search} />
                </Card.Body>
            </Card>
        </>
    );
};

export default Search;