import { Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { Routes, Route, Link } from 'react-router-dom';

import Home from './Home';
import About from './About';
import Search from './Search';
import Contact from './Contact';

const Navigation = () => {
    return (
        <>
            <Navbar className='navigation' fixed="top" variant='dark' expand="lg">
                <Navbar.Brand as={Link} to='*'>Find a Mentor</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link as={Link} to='*'>Home</Nav.Link>

                        <NavDropdown title="Search" id="basic-nav-dropdown">
                            <NavDropdown.Item as={Link} to='/class'>Search by Class</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to='/mentor'>Search by Mentor</NavDropdown.Item>
                        </NavDropdown>

                        <Nav.Link as={Link} to='/about'>About</Nav.Link>
                        <Nav.Link as={Link} to='/contact'>Contact Us</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
            <Routes>
                <Route path='*' element={<Home />} />
                <Route path='/about' element={<About />} />
                <Route path='/search' element={<Search />} />
                <Route path='/class' element={<Search search='class'/>} />
                <Route path='/mentor' element={<Search search='mentor'/>} />
                <Route path='/contact' element={<Contact />} />
            </Routes>
        </>
    );
};

export default Navigation;