import { Link } from 'react-router-dom';
import { Card, Button } from 'react-bootstrap';
import '../styles/Home.css';

const Home = () => {
    return (
        <>
            <Card className="home-card">
                <Card.Body>
                    <Card.Title className='card-title'>Welcome!</Card.Title>
                    <Card.Text>We'll help you find a mentor at Purdue University Fort Wayne.</Card.Text>
                    <Button variant="primary" as={Link} to='/search'>Search now</Button>
                </Card.Body>
            </Card>

        </>
    );
};

export default Home;